const swiper = new Swiper('.swiper-container', {
	// Optional parameters
	loop: false,
	slidesPerView: 4,
	speed: 1000, // Продолжительность перехода в миллисекундах
	effect: 'slide', // Эффект перехода ('slide' по умолчанию)
	grabCursor: true, // Изменить курсор при перетаскивании
	simulateTouch: true, // Разрешить взаимодействие с сенсорными экранами
	touchRatio: 1, // Коэффициент чувствительности к касанию
	touchAngle: 45,
	allowTouchMove: true, // Разрешить перетаскивание слайдов курсором
	mousewheel: {
		enabled: true, // Включить управление слайдером с помощью колеса мыши
	},
	// Брейкпойнты по аналогии с Bootstrap
	breakpoints: {
		320: { // Для экранов от 320px и выше
			slidesPerView: 1,
			spaceBetween: 0
		},
		576: { // Эквивалентно Bootstrap sm
			slidesPerView: 2,
			spaceBetween: 20
		},
		768: { // Эквивалентно Bootstrap md
			slidesPerView: 2,
			spaceBetween: 40
		},
		992: { // Эквивалентно Bootstrap lg
			slidesPerView: 3,
			spaceBetween: 20
		},
		1200: { // Эквивалентно Bootstrap xl
			slidesPerView: 4,
			spaceBetween: 30
		}
	},
	// If we need pagination
	pagination: {
		el: '.swiper-pagination',
		type: 'fraction',
		renderFraction: function (currentClass, totalClass) {
			return '<span class="' + currentClass + '"></span>' +
				' из ' +
				'<span class="' + totalClass + '"></span>';
		},
	},


	// Navigation arrows
	navigation: {
		nextEl: '.next-class', // должен быть .next-class
		prevEl: '.prev-class'  // должен быть .prev-class
	},


	// And if we need scrollbar
	scrollbar: {
		el: '.swiper-scrollbar',
	},
});
